          c0.076-0.415,1.046-7.832-3.105-12.045c-4.15-4.212-7.552-5.689-16.8-6.097c-9.247-0.408-15.999,2.331-19,6.576
          c-2.098,2.966-3.036,6.854-3.412,8.927c-0.136,0.754,0.442,1.448,1.208,1.447l9.429-0.003c0.262,0,0.517-0.084,0.728-0.24
          l5.259-3.871c0.547-0.404,0.663-1.176,0.256-1.722l-2.363-3.175l14.818,2.167c0.67,0.099,1.135,0.722,1.037,1.392l-2.17,14.833
          l-2.285-3.07c-0.409-0.551-1.19-0.658-1.733-0.239c-1.542,1.191-4.659,3.598-8.412,6.479c-5.378,4.131-10.792,9.398-13.67,14.051
          c-2.879,4.652-3.421,10.408-3.421,10.408h44.471c0.678,0,1.228-0.55,1.228-1.228v-9.658c0-0.677-0.55-1.227-1.228-1.227H22.361z"/>
      </svg>
    </a>

    <h1>–û–Ω–ª–∞–π–Ω —á–∞—Ç</h1> 
      <!-- <router-link to="/" class="link">–ù–∞ –≥–ª–∞–≤–Ω—É—é</router-link>    
      <router-link to="/chat" class="link">–ß–∞—Ç</router-link>       -->
    <transition  :name="transitionName" mode="out-in" appear >
      <router-view></router-view>
    </transition>
  
  </div>
  
</template>

<script>
import firebase from 'firebase';

	var config = {
		apiKey: "AIzaSyBlbfDC9l35RrE-dD9Mz_0bPZoUBcNfmg0",
		authDomain: "offlinechat-2up.firebaseapp.com",
		databaseURL: "https://offlinechat-2up.firebaseio.com/",
		projectId: "offlinechat-2up",
		storageBucket: "offlinechat-2up.appspot.com",
		messagingSenderId: "612784355266"
	};

	firebase.initializeApp(config)
export default {
  name: 'App',
  data: () => ({
    transitionName: '',
  }),
  watch: {
    '$route' (to, from) {
      const toDepth = to.path.split('/')[1].length
      const fromDepth = from.path.split('/')[1].length
      this.transitionName = toDepth < fromDepth ? 'slide-fade-right' : 'slide-fade-left'
    }
  }
}
</script>

<style lang="scss">

html {
  margin-left: calc(100vw - 100%)
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;  
}


$color-time: 12s;
$step: $color-time/4;

stop {
    animation: colors $color-time infinite;
}

.stop-2 {
    animation-delay: -$step;
}
.stop-3 {
    animation-delay: -$step*2;
}

@keyframes colors {
    15% {
        stop-color: crimson;
    }
    30% {
        stop-color: gold;
    }
    45% {
        stop-color: teal;
    }
    60% {
        stop-color: yellowgreen;
    }
    75% {
        stop-color: orangered;
    }
    90% {
        stop-color: purple;
    }
}


img { 
  margin-bottom: 20px;
}

h1 {
  margin-bottom: 30px;
}

.link {
  margin-right: 20px;
}
//left
.slide-fade-left-enter {
  transform: translateX(300px);
}

.slide-fade-left-enter-active {
  transform: translateX(300);
  transition: all .3s ease;
}
.slide-fade-left-leave-active {
  transform: translateX(-300px);
  transition: all .3s ease;
}
.slide-fade-left-enter,
.slide-fade-left-leave-active,{
  opacity: 0;
  transition: all .5s ease;
}

//right
.slide-fade-right-enter {
  transform: translateX(-300px);
}

.slide-fade-right-enter-active {
  transform: translateX(-300);
  transition: all .3s ease;
}
.slide-fade-right-leave-active {
  transform: translateX(300px);
  transition: all .3s ease;
}
.slide-fade-right-enter,
.slide-fade-right-leave-active {
  opacity: 0;
  transition: all .5s ease;
}

.header-panel {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 20px;
    margin-bottom: 20px;
}

.network-indicator {

  &__status {
      margin: 0;
      position: relative;
      padding-left: 15px;

      &::before {
          position: absolute;
          content: "";
          top: 50%;
          left: 0;
          transform: translateY(-50%);

          width: 10px;
          height: 10px;

          border-radius: 50%;
      }

      &_online::before {
          background-color: lawngreen;   
      }

      &_offline::before {
          background-color: lightcoral;   
      }
  }
}

</style>



